import { FacebookOutlined, SaveOutlined } from '@ant-design/icons'
import { Breadcrumb, Layout, Spin } from 'antd'
import Search from 'antd/lib/input/Search'
import { reverse, uniqBy } from 'lodash'
import React, { FC, useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useFacebookPage } from '../hooks/useFacebookPage'
import { PageContext, PageContextValue } from '../PageContext'
import { FbApiCall, getAllResults } from '../util/fb'
import { FacebookPaginatedResponse } from '../util/fb.definitions'


interface GiveawayFbData {
  comments: FacebookPaginatedResponse<{
    message: string,
    from: {
      id: string,
      name: string
    },
    message_tags?: any[]
  }>,
  reactions: FacebookPaginatedResponse<{
    id: string
    name: string
  }>
}

const getPostId = (data: string) => reverse(data.split('/').filter(r => r != null && r !== ''))[0]
const hasTagged = (c: GiveawayFbData['comments']['data'][0]) => (c.message_tags?.length ?? 0) > 0 || c.message.split(/\s/g).length >= 2

const GiveawayData = () => {
  const { page } = useContext(PageContext) as PageContextValue
  const [postUrl, setPostUrl] = useState<string>()
  const [comments, setComments] = useState<GiveawayFbData['comments']['data']>([])
  const [reactions, setReactions] = useState<GiveawayFbData['reactions']['data']>([])
  const postId = getPostId(postUrl ?? '')
  useEffect(() => {
    if (postId === undefined || postUrl === '') {
      return
    }
    const effect = async () => {
      const reply = await FbApiCall<GiveawayFbData>(`${page.id}_${postId}`, { fields: 'comments.limit(100).live_filter(no_filter).filter(stream){message,from,message_tags,id},reactions.limit(100){username,name,id}', access_token: page.access_token })
      const allComments = await getAllResults(reply.comments)
      const allReactions = await getAllResults(reply.reactions)
      setComments(allComments)
      setReactions(allReactions)
      console.log(reply)
      console.log(allComments)
      console.log(allReactions)
    }
    effect()
  }, [postUrl, page.id, page.access_token, postId])

  const didReact = reactions.map(r => r.id)
  const participants = uniqBy(comments.filter(hasTagged).filter(c => didReact.includes(c.from.id)), (c) => c.from.id)
  return (
    <>
      <Search
        prefix={<FacebookOutlined />}
        placeholder={`https://www.facebook.com/${page.name}/photos/a.00000000000000000/XXXXXXXXXXXXX/`}
        defaultValue={postUrl}
        enterButton={<SaveOutlined />}
        size="large"
        onSearch={(value) => setPostUrl(value)}
      />
      <p>Total: {participants.length}</p>
      {participants.map(p => (
        <p key={`part-list-${p.from.id}`}><small>{p.from.id}</small> {p.from.name}</p>
      ))}
    </>
  )
}

const { Content } = Layout

export const AdvancedGiveaway: FC = () => {
  const { pageId } = useParams<{ pageId: string }>()
  const [loaded, page, videos] = useFacebookPage(pageId)

  if (page === undefined) {
    return <Spin />
  }

  return (
    <PageContext.Provider value={{ page, videos }}>
      <Content style={{ margin: '0 16px' }}>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>{page?.name}</Breadcrumb.Item>
          <Breadcrumb.Item>Advanced Giveaway</Breadcrumb.Item>
        </Breadcrumb>
        <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
          {!loaded && <GiveawayData />}
        </div>
      </Content>
    </PageContext.Provider>
  )
}
