import { Button } from 'antd';
import React, { useContext, useEffect, useState } from 'react';
import { LiveVideo } from '../definitions';
import { useParams } from 'react-router-dom';
import { loadCachedMessages } from '../loadCachedMessages';
import { PageContext, PageContextValue } from '../PageContext';
import { useVideoCustomState } from './util';
import { generateNotifies } from './Notifies';
import { sampleSize } from 'lodash';
import { VideoComment } from '../util/fb.definitions';

export function GenerateGiveawayWinners() {
  const { id } = useParams<{ id: string; }>();
  const { page, videos } = useContext(PageContext) as PageContextValue;
  const [messages, loadMessages] = useState<VideoComment[]>([]);
  const [videoState, setVideoState] = useVideoCustomState(id)();
  const video = videos.find(v => v.id === id) as LiveVideo;

  useEffect(() => {
    const effect = async () => {
      loadMessages(await loadCachedMessages(id));
    };
    effect();
  });

  if (messages.length === 0 || video.status === 'LIVE' || (videoState.winners?.length ?? 0) > 0) {
    return <></>;
  }

  const notifies = generateNotifies(messages, page.name);

  return <Button type="ghost" onClick={() => {
    const winners = sampleSize(notifies, 3).map(n => n.from);
    setVideoState(current => ({ ...current, winners }));
  }}>Giveaway!</Button>;
}
